@tailwind base;
@tailwind components;
@tailwind utilities;

main {
    width: 100%;
    height: 100%;
}

::-webkit-scrollbar {
    width: 4px;
    background-color: #e0e0e0;
}

::-webkit-scrollbar-thumb {
    background-color: #ccc;
    border-radius: 4px;

}

::-webkit-scrollbar-thumb:hover {
    background-color: #aaa;
}